<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="150px"
			>
			<el-form-item class="add-item" label="" prop="tuijianbiaoti">
				<el-input v-model="ruleForm.tuijianbiaoti" 
					placeholder="" clearable :disabled=" false  ||ro.tuijianbiaoti"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.tuijiantupian)" prop="tuijiantupian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.tuijiantupian?ruleForm.tuijiantupian:''"
					@change="tuijiantupianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="tuijiantupian">
				<img v-if="ruleForm.tuijiantupian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.tuijiantupian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.tuijiantupian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="schoolname">
				<el-select  @change="schoolnameChange" v-model="ruleForm.schoolname" placeholder="">
					<el-option
						v-for="(item,index) in schoolnameOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="specname">
				<el-input v-model="ruleForm.specname" 
					placeholder="" clearable :disabled=" false  ||ro.specname"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="speccode">
				<el-input v-model="ruleForm.speccode" 
					placeholder="" clearable :disabled=" false  ||ro.speccode"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="diqu">
				<el-input v-model="ruleForm.diqu" 
					placeholder="" clearable :disabled=" false  ||ro.diqu"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="yuanxiaocengci">
				<el-select v-model="ruleForm.yuanxiaocengci" placeholder="" :disabled=" false  ||ro.yuanxiaocengci" >
					<el-option
						v-for="(item,index) in yuanxiaocengciOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="luqufenshuxian">
				<el-input-number v-model="ruleForm.luqufenshuxian" placeholder="" :disabled=" false ||ro.luqufenshuxian"></el-input-number>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="luqubili">
				<el-input v-model="ruleForm.luqubili" 
					placeholder="" clearable :disabled=" false  ||ro.luqubili"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="tuijianshijian">
				<el-date-picker
					:disabled=" false  ||ro.tuijianshijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.tuijianshijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="zhanghao">
				<el-input v-model="ruleForm.zhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.zhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="xingming">
				<el-input v-model="ruleForm.xingming" 
					placeholder="" clearable :disabled=" false  ||ro.xingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="tuijianxiangqing">
				<editor 
					v-model="ruleForm.tuijianxiangqing" 
					class="editor" 
					myQuillEditor="tuijianxiangqing"
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					tuijianbiaoti : false,
					tuijiantupian : false,
					schoolname : false,
					specname : false,
					speccode : false,
					diqu : false,
					yuanxiaocengci : false,
					luqufenshuxian : false,
					luqubili : false,
					tuijianshijian : false,
					tuijianxiangqing : false,
					zhanghao : false,
					xingming : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					tuijianbiaoti: '',
					tuijiantupian: '',
					schoolname: '',
					specname: '',
					speccode: '',
					diqu: '',
					yuanxiaocengci: '',
					luqufenshuxian: '',
					luqubili: '',
					tuijianshijian: '',
					tuijianxiangqing: '',
					zhanghao: '',
					xingming: '',
				},
				schoolnameOptions: [],
				yuanxiaocengciOptions: [],


				rules: {
					tuijianbiaoti: [
					],
					tuijiantupian: [
					],
					schoolname: [
					],
					specname: [
					],
					speccode: [
					],
					diqu: [
					],
					yuanxiaocengci: [
					],
					luqufenshuxian: [
						{ validator: this.$validate.isNumber, trigger: 'blur' },
					],
					luqubili: [
					],
					tuijianshijian: [
					],
					tuijianxiangqing: [
					],
					zhanghao: [
					],
					xingming: [
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='tuijianbiaoti'){
							this.ruleForm.tuijianbiaoti = obj[o];
							this.ro.tuijianbiaoti = true;
							continue;
						}
						if(o=='tuijiantupian'){
							this.ruleForm.tuijiantupian = obj[o]?obj[o].split(",")[0]:'';
							this.ro.tuijiantupian = true;
							continue;
						}
						if(o=='schoolname'){
							this.ruleForm.schoolname = obj[o];
							this.ro.schoolname = true;
							continue;
						}
						if(o=='specname'){
							this.ruleForm.specname = obj[o];
							this.ro.specname = true;
							continue;
						}
						if(o=='speccode'){
							this.ruleForm.speccode = obj[o];
							this.ro.speccode = true;
							continue;
						}
						if(o=='diqu'){
							this.ruleForm.diqu = obj[o];
							this.ro.diqu = true;
							continue;
						}
						if(o=='yuanxiaocengci'){
							this.ruleForm.yuanxiaocengci = obj[o];
							this.ro.yuanxiaocengci = true;
							continue;
						}
						if(o=='luqufenshuxian'){
							this.ruleForm.luqufenshuxian = obj[o];
							this.ro.luqufenshuxian = true;
							continue;
						}
						if(o=='luqubili'){
							this.ruleForm.luqubili = obj[o];
							this.ro.luqubili = true;
							continue;
						}
						if(o=='tuijianshijian'){
							this.ruleForm.tuijianshijian = obj[o];
							this.ro.tuijianshijian = true;
							continue;
						}
						if(o=='tuijianxiangqing'){
							this.ruleForm.tuijianxiangqing = obj[o];
							this.ro.tuijianxiangqing = true;
							continue;
						}
						if(o=='zhanghao'){
							this.ruleForm.zhanghao = obj[o];
							this.ro.zhanghao = true;
							continue;
						}
						if(o=='xingming'){
							this.ruleForm.xingming = obj[o];
							this.ro.xingming = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
					}
				});
				this.$http.get('option/hisyearscore/schoolname', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.schoolnameOptions = res.data.data;
					}
				});
				this.yuanxiaocengciOptions = "985,211,".split(',')

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},
			// 
			schoolnameChange () {
				this.$http.get('follow/hisyearscore/schoolname?columnValue=' + this.ruleForm.schoolname, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						if(res.data.data.specname){
							this.ruleForm.specname = res.data.data.specname
						}
						if(res.data.data.speccode){
							this.ruleForm.speccode = res.data.data.speccode
						}
					}
				});
			},

			// 
			// 
			info() {
				this.$http.get(`tuijianyuanxiao/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`tuijianyuanxiao/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			tuijiantupianUploadChange(fileUrls) {
				this.ruleForm.tuijiantupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

